<template>
  <div>
    <div id="ck_query">
      <!-- 导航 -->
      <div class="nav">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '' }">首页</el-breadcrumb-item>
          <!-- <el-breadcrumb-item :to="{ path: '' }">创客后台</el-breadcrumb-item> -->
          <el-breadcrumb-item>用户管理</el-breadcrumb-item>
        </el-breadcrumb>
        <div class="searchGroup">
          <el-row :gutter="10">
            <el-col :span="4">
              &nbsp;
            </el-col>           
            <el-col :span="8">
              <div class="grid-content bg-purple">
                <el-input v-model="search_name" placeholder="请输入姓名"></el-input>
              </div>
            </el-col>
             <el-col :span="8">
              <div class="grid-content bg-purple">
                <el-input v-model="search_phone" placeholder="请输入手机号码"></el-input>
              </div>
            </el-col>
            <el-col :span="4">
              <div class="grid-content bg-purple search-button">
                <el-button type="primary" icon="el-icon-search" @click="searchFun">&nbsp;搜索&nbsp;</el-button>
              </div>
            </el-col>
          </el-row>
        </div>
      </div>

      <!-- 表格区域 -->
      <div class="table_container">
        <div class="muti_container">
          <!-- <div class="mutiBar">
            <div class="function" @click="jumptoRegister">
              <i class="el-icon-plus"></i>
              注册
            </div>
          </div> -->
          <el-table
                v-loading="loading"
                height="100%"
                border
                :data="tableList"
                style="width: 100%;"
                row-key="USER_UUID"
            >
            <!-- <el-table-column type="expand">
              <template slot-scope="props">
                <div class="morePanel">
                  <div class="sideBox">
                    <div class="longBoxLeft">
                      <div class="bankInfo">
                        <div class="draw">
                          <img src="../../assets/imgs/user.png" alt class="ckimg" />
                          <br />
                          <span @click="updateCk(props.row)">编辑创客</span>
                        </div>
                      </div>
                     
                    </div>                    
                  </div>
                </div>
              </template>
            </el-table-column> -->
            <el-table-column label="姓名" prop="xm"></el-table-column>
            <el-table-column label="手机号码" prop="phone"></el-table-column>
            <el-table-column label="推荐人" prop="sjXm"></el-table-column>
            <el-table-column label="身份证件号码" prop="sfzjhm"></el-table-column>
            <el-table-column label="职位" prop="zw"></el-table-column>
            <el-table-column label="支付宝号码" prop="zfbzh"></el-table-column>
            <el-table-column label="录入时间" prop="lrsj"></el-table-column>
            <el-table-column label="操作" width="150" align="center">
              <template slot-scope="props">
                <el-button type="success" size="mini" style="margin:0 auto;" @click.native="updateCk(props.row)" round>编辑</el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="table_info_bar_002">
            <div class="block">
              <el-pagination
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage"
                :page-sizes="[100, 200, 300, 400]"
                :page-size="10"
                layout="total, prev, pager, next, jumper"
                :total="totalSize"
              ></el-pagination>
            </div>
          </div>
        </div>
      </div>

    <!-- 编辑用户 -->
      <el-dialog title="编辑用户" :visible.sync="isShowAddDialog" width="70%" :close-on-click-modal="false">
        <div style="margin: -14px 5px;">
          <el-form :model="userData">
              <el-row>
                <el-col :span="12">
                  <div class="grid-content">
                    <el-form-item label="姓名" :label-width="formLabelWidth" style="margin-bottom: 10px;">
                      <el-input v-model="userData.xm" auto-complete="off" placeholder="请输入姓名"></el-input>
                    </el-form-item>
                  </div>
                </el-col>
                <el-col :span="12">
                  <div class="grid-content">
                    <el-form-item label="手机号码" :label-width="formLabelWidth" style="margin-bottom: 10px;">
                      <el-input v-model="userData.phone" auto-complete="off" placeholder="请输入手机号码" readonly="readonly"></el-input>
                    </el-form-item>
                  </div>
                </el-col>     
              </el-row>
              <el-row>
                 <el-col :span="12">
                  <div class="grid-content">
                    <el-form-item label="身份证件号码" :label-width="formLabelWidth">
                      <el-input v-model="userData.sfzjhm" placeholder="请输入身份证件号码"></el-input>
                    </el-form-item>
                  </div>
                </el-col>

                <el-col :span="12">
                  <div class="grid-content">
                    <el-form-item label="用户类型" :label-width="formLabelWidth">
                      <el-select v-model="userData.userType" placeholder="请选择用户类型">
                        <el-option v-for="item in userTypeList" :key="item.dm" :label="item.dmmc" :value="item.dm"></el-option>
                      </el-select>
                    </el-form-item>
                  </div>
                </el-col> 
              </el-row>
              <el-row>
                <el-col :span="12">
                  <div class="grid-content">
                    <el-form-item label="上级人员姓名" :label-width="formLabelWidth">
                      <el-input v-model="userData.sjXm"  @focus="dialogTableGetSstjr=true" placeholder="请输入上级人员姓名"></el-input>
                    </el-form-item>
                  </div>
                </el-col>
                <el-col :span="12">
                  <div class="grid-content">
                    <el-form-item label="支付宝账号" :label-width="formLabelWidth">
                      <el-input type="number" v-model="userData.zfbzh" placeholder="请输入支付宝账号"></el-input>
                    </el-form-item>
                  </div>
                </el-col>
              </el-row>
              <el-row>                
                <el-col :span="12">
                  <div class="grid-content">
                    <el-form-item label="职位" :label-width="formLabelWidth">
                      <el-select v-model="userData.zw" placeholder="请选择职位">
                        <el-option v-for="item in zwList" :key="item.dm" :label="item.dmmc" :value="item.dm"></el-option>
                      </el-select>
                    </el-form-item>
                  </div>
                </el-col>
              </el-row>     
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="cancel">取 消</el-button>
            <el-button type="primary" @click="valiteForm">确定</el-button>
          </div>
        </div>
      </el-dialog>

      <!-- 查询所属推荐人弹出层 -->
      <el-dialog title="选择推荐人" :visible.sync="dialogTableGetSstjr" id="ssckForm" width="40%" :close-on-click-modal="false">
        <el-input placeholder="请输入联系电话进行查询" v-model="searchSstjrValue" class="input-with-select" id="xjryInput" style="margin: 1px 0px 20px;">
          <el-button slot="append" icon="el-icon-search" @click="searchSstjr"></el-button>
        </el-input>
        <el-table :data="gridData" @row-click="handletableSstjrClick" height="400">
          <el-table-column property="xm" label="姓名"></el-table-column>
          <el-table-column property="phone" label="手机号码"></el-table-column>
        </el-table>
        <el-pagination small layout="prev, pager, next" :total="gridDataSize"  @current-change="gridCurrentChange" :current-page="gridCurrentPage" style="text-align:center;position:relative;top:10px;">
        </el-pagination>
      </el-dialog>

    </div>
  </div>
</template>

<style lang="scss" scoped>
@import './userManage.scss';
</style>

<script>
import Axios from 'axios';
import { setTimeout } from 'timers';
var menu_uuid = '35343920DA49B7876A2CFA235458B832';
export default {
  data() {
    return {
      loading:false,
      isShowAddDialog:false,
      dialogTableGetSstjr:false,
      formLabelWidth:'100px',
      search_name:'',
      search_phone:'',
      tableList:[],
      expands:'',
      currentPage:1,
      totalSize:0,
      userData:{
        userUuid:'',
        xm:'',
        phone:'',
        userType:'',
        sfzjhm:'',
        zfbzh:'',
        sjUserUuid:'',
        sjXm:'',
        zw:''
      },
      gridData:[],
      gridDataSize:1,
      gridCurrentPage:1,
      searchSstjrValue:'',
      userTypeList:[],
      zwList:[],
    };
  },
  methods: {
    //编辑弹出层关闭
    cancel(){
      this.isShowAddDialog=false;
    },
    //搜索推荐人
    searchSstjr(){
      this.querySstjr(this.searchSstjrValue,1);
    },
    //推荐人选择行
    handletableSstjrClick(row){
      console.log(row)
      this.dialogTableGetSstjr=false;
      this.userData.sjXm=row.xm;
      this.userData.sjUserUuid=row.userUuid;     
    },
    //所属推荐人分页
    gridCurrentChange(val){
      this.querySstjr(this.searchSstjrValue,val);
    },
    //保存编辑用户信息
    valiteForm(){
      if( this.userData.xm.length<=0 ){
          this.$message({
            type:'warning',
            message:'请输入姓名'
          })
          return false;
        }
        if( this.userData.sfzjhm.length<=0 ){
          this.$message({
            type:'warning',
            message:'请输入身份证件号码'
          })
          return false;
        }
        if( this.userData.sjUserUuid.length<=0){
          this.$message({
            type:'warning',
            message:'请选择上级人员'
          })
          return false;
        }
        if( this.userData.zfbzh=="" ||this.userData.zfbzh==null){
          this.$message({
            type:'warning',
            message:'请输入支付宝账号'
          })
          return false;
        }
        this.editUser();
    },
    //编辑用户
    editUser(){
        var that=this;
        var json={
          "userUuid":this.userData.userUuid,
          "xm":this.userData.xm,
          "phone":this.userData.phone,
          "userType":this.userData.userType,
          "sfzjhm":this.userData.sfzjhm,
          "zfbzh":this.userData.zfbzh,
          "sjUserUuid":this.userData.sjUserUuid,
          "sjXm":this.userData.sjXm,
          "zw":this.userData.zw
        }
         $.ajax({
            type: 'POST',
            async: true,
            url:this.projectName+'user/updateUser',
            data:JSON.stringify(json),
            headers:{
                'Authorization':sessionStorage.getItem("tokenHead")+sessionStorage.getItem("token")
            },
            contentType:"application/json",
            dataType: "json",
            timeout: 60000,
            success: function(data, textStatus, jqXHR) {
            if( data.code==200 ){
              that.$message({
                type:"success",
                message:data.message
              })
              that.querTableData(that.search_name,that.search_phone,1);
              that.isShowAddDialog=false;
            }else{
              that.$message({
                type:"warning",
                message:data.message
              })
            }
            console.log( data )
          },
          error: function(xhr, textStatus) {
            console.log(xhr);
          }
          });
    },
    //点击编辑人员
    updateCk(row){
      console.log(row);
      this.userData={
        userUuid:row.userUuid,
        xm:row.xm,
        phone:row.phone,
        userType:row.userType,
        sfzjhm:row.sfzjhm ||'',
        zfbzh:row.zfbzh || '',
        sjUserUuid:row.sjUserUuid ||'',
        sjXm:row.sjXm ||'',
        zw:row.zw
      }
      this.isShowAddDialog=true;
      this.querySstjr(this.searchSstjrValue,1);
    },
    //表格分页
    handleCurrentChange(val){
      console.log(val);
      this.querTableData(this.search_name,this.search_phone, val);
    },
    // 点击搜索
    searchFun() {
      this.querTableData(this.search_name,this.search_phone,  1);
    },
    // 查询表格数据
    querTableData(_xm, _phone,_start) {
      var that = this;
      this.loading = true;
      Axios({
        method: 'post',
        url: this.projectName + 'user/querySubordinateUsers',
        params: {
          xm: _xm,
          phone:_phone,
          pageNum: _start
        },
        headers:{
            'Authorization':sessionStorage.getItem("tokenHead")+sessionStorage.getItem("token")
        },
        contentType:"application/json",
        dataType: "json",
      })
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            that.tableList = response.data.data.list;
            that.totalSize = response.data.data.total;
          } else {
            that.tableList = [];
            that.totalSize = 0;
          }
          that.loading = false;
        })
        .catch(function(error) {
          that.loading = false;
        });
    },
    //所属推荐人信息
    querySstjr(_input,_start){
      var that=this;
      Axios({
        method: 'post',
        url: this.projectName + 'user/querySubordinateUsers',
        params:{          
          xm: '',
          phone:_input,
          pageNum: _start
        },
        headers:{
            'Authorization':sessionStorage.getItem("tokenHead")+sessionStorage.getItem("token")
        },
        contentType:"application/json",
        dataType: "json",
        }).then(function (response) {
          console.log(response.data)
        if(response.data.result=="Fail"){
          that.gridData=[];
          that.gridDataSize=0;
        }else{
          that.gridData=response.data.data.list;
          that.gridDataSize=response.data.data.total;       
        }
      })
      .catch(function (error) {
        
      }) 
      
    },
    //初始化
    initData(){
        var that=this;
        Axios({
          method:'post',
          url:this.projectName+'common/getCodeTable',
          params:{
            dmType:'角色类型'
          },
          headers:{
              'Authorization':sessionStorage.getItem("tokenHead")+sessionStorage.getItem("token")
          },
          contentType:"application/json",
          dataType: "json",
          }).then(function (response) {
            console.log("角色类型:",response.data);
            if (response.data.code == 200) {
              that.userTypeList = response.data.data;     
            }
        })
        .catch(function (error) {
          
        })

       Axios({
          method:'post',
          url:this.projectName+'common/getCodeTable',
          params:{
            dmType:'职位'
          },
          headers:{
              'Authorization':sessionStorage.getItem("tokenHead")+sessionStorage.getItem("token")
          },
          contentType:"application/json",
          dataType: "json",
          }).then(function (response) {
            console.log("职位:",response.data);
            if (response.data.code == 200) {
              that.zwList = response.data.data;     
            }
        })
        .catch(function (error) {
          
        })
      
     }
  },
  watch: {
  },
  created: function() {
    this.initData();
  },
  mounted: function() {
    this.querTableData('','', 1);
  }
};
</script>


